 satisfecho?")]),_c('li',{staticClass:"mt-2"},[_c('i',{staticClass:"fas fa-database"}),_vm._v("¿Cuál aspecto está siendo su mayor debilidad?")]),_c('li',{staticClass:"mt-2"},[_c('i',{staticClass:"fas fa-database"}),_vm._v("¿Qué necesita para mejorar?")]),_c('li',{staticClass:"mt-2"},[_c('i',{staticClass:"fas fa-database"}),_vm._v("¿Qué oportunidades tiene?")])])]),_c('div',{staticClass:"row px-3",attrs:{"numero":"8","titulo":"Seguimiento y evaluación de resultados"}},[_c('p',{staticClass:"px-0"},[_vm._v("Se debe hacer revisión periódica para verificar que lo planeado, está siendo ejecutado en los tiempos establecidos y si hay variaciones se debe hacer un plan de mejora que permita corregir errores y direccionar nuevamente la planeación para el logro del objetivo. Por último, se debe hacer una evaluación de los resultados de los planes; es clave analizar cómo fue la ejecución de las acciones esto ayuda a consolidar los procesos y aprender de los errores, los que puede llevar a reajustar el plan inicial; por supuesto, todo en función del propósito general.")])])])],1)],1)],1)}
